<template>
	<div id="login">
		<div id="login_head">
			<van-nav-bar left-arrow cover @click-left="onClickLeft" :border="false" />
		</div>
		<div id="login_img">
			<van-image width="1.8rem" height="1.8rem" :src="require('@/assets/images/logo.png')" />
		</div>
		<div id="login_form">
			<van-form @submit="onSubmit">
				<van-field v-model="username" @keyup="but()" name="用户名" type="digit" label="账号" placeholder="请输入手机号" :rules="[{pattern, message: '11位手机号码' }]"
				 maxlength="11" />
				<van-field v-model="password" :mask="false" @keyup="but()" type="password" name="密码" label="密码" placeholder="请输入密码"
				 :rules="[{validator, message: '至少六位密码' }]" />
				<div style="margin: 16px;">
					<van-button round block type="info" native-type="submit" v-bind:disabled="dis">
						登录
					</van-button>
				</div>
			</van-form>
		</div>
	</div>
</template>

<script>
	import {
		NavBar,
		Icon,
		Image as VanImage,
		Form,
		Button,
		Field,
		Toast
	} from 'vant';
	export default {
		components: {
			[NavBar.name]: NavBar,
			[Icon.name]: Icon,
			[VanImage.name]: VanImage,
			[Form.name]: Form,
			[Button.name]: Button,
			[Field.name]: Field,
			[Toast.name]: Toast
		},
		data() {
			return {
				username: '',
				password: '',
				pattern: /1\d{10}/,
				dis: true
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			validator(val) {
				return /^.{6,}$/.test(val);
			},
			but() {
				if (this.username != "" && this.password != "") {
					this.dis = false;
				} else {
					this.dis = true;
				}
			},
			onSubmit() {
				Toast.setDefaultOptions({
					duration: 1000
				});

				Toast.loading('登陆中...');

				setTimeout(() => {
					this.$router.push('/index');
				}, 2000);
			}
		},
	}
</script>

<style>
	/* 重写头部样式 */
	#login #login_head .van-icon-arrow-left::before {
		color: #1989fa;
		font-size: 0.5rem;
	}

	#login #login_head .van-nav-bar {
		height: 1rem;
	}

	/* 头像 */
	#login #login_img {
		width: 1.8rem;
		height: 1.8rem;
		margin: 0.5rem auto;
		/* border: 1px solid saddlebrown; */
	}

	/* form表单 */
	#login #login_form {
		width: 100%;
		height: 4rem;
		/* border: 1px solid red; */
	}

	#login #login_form .van-field__control,
	.van-field__label {
		height: 1rem;
		font-size: 0.4rem;
	}

	#login #login_form .van-cell {
		padding-left: 0.4rem;
	}

	#login #login_form .van-field__label {
		line-height: 0.8rem;
		width: 1.4rem;
		color: #333333;
	}

	#login #login_form .van-button--block {
		height: 0.8rem;
		font-size: 0.4rem;
	}

	/* 登陆提示 */
	#login #login_form .van-field__value {
		display: flex;
	}

	#login #login_form .van-field__error-message {
		width: 3rem;
		line-height: 0.8rem;
		text-align: center;
		font-size: 0.3rem;
	}

</style>
