<template>
	<div id="add_bank_card">
		<div id="add_bank_card_head">
			<van-nav-bar :border="false" title="添加银行卡" left-text="返回" left-arrow @click-left="onClickLeft">
				<template #right>
					<van-icon name="question-o" size="0.5rem" />
				</template>
			</van-nav-bar>
		</div>
		<div id="Tips1">请绑定持卡人本人银行卡</div>
		<div id="add_bank_card_from">
			<van-cell-group>
				<van-field label="持卡人" right-icon="info" @click-right-icon="onClickRight(1)" value="花开富贵" readonly />
				<van-field v-model="digitVal" type="digit" autofocus="true" label="卡号" placeholder="无需网银/免手续费" :clearable="true"
				 :clear="checkCard()" />
			</van-cell-group>
		</div>
		<div id="Tips1">支付宝智能加密，保障你的用卡安全,<span style="color: #108ee9;" @click="onClickRight(2)">了解安全保障</span></div>
		<div id="submit">
			<van-button @click="nextStep()" type="primary" size="large" :disabled="val">下一步</van-button>
		</div>
		<div>
			<van-popup v-model="show1" :close-on-click-overlay="false">
				<span>持卡人说明</span>
				<div style="line-height: 0.6rem;">为保证账户资金安全，只能绑定认证用户本人的银行卡</div>
				<p @click="onClickRight(0)">知道了</p>
			</van-popup>
		</div>
		<div class="security">
			<van-popup v-model="show2" :close-on-click-overlay="false">
				<span>安全保障</span>
				<div style="line-height: 0.6rem;">1.安全保障：账号保护、实时监控、紧急冻结等多重金融安全保障。<br>2.双重保障：每次付款均验证支付密码，大额支付还需要短信验证<br>3.隐私保护：高度强调数据加密，保护用户隐私信息。<br>4.支付理赔：支付安全由才富保险承保。<br>5.了解详情,请致电客服99999。</div>
				<p @click="onClickRight(0)">知道了</p>
			</van-popup>
		</div>
	</div>
</template>

<script>
	import bankInfo from '../../utils/banks.js'
	import {
		NavBar,
		Toast,
		Icon,
		Field,
		Button,
		Popup
	} from 'vant';
	export default {
		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Icon.name]: Icon,
			[Field.name]: Field,
			[Button.name]: Button,
			[Popup.name]: Popup
		},
		data() {
			return {
				digitVal: '',
				show1: false,
				show2: false,
				val: true,
				bankList: [],
				cardNo: ''
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			onClickRight(num) {
				if (num == 1) {
					this.show1 = true;
				} else if (num == 2) {
					this.show2 = true;
				} else {
					this.show1 = false;
					this.show2 = false;
				}
			},
			async nextStep() {
				// 判断卡号是否已添加过
				var val = this.digitVal;

				let arrAll = JSON.parse(localStorage.getItem("bankList"));
				// console.log(arrAll);
				if (arrAll) {
					// 循环判断
					for (var i = 0; i < arrAll.length; i++) {
						if (arrAll[i].cardNo == val) {
							Toast.fail('该银行卡已经添加');
							return;
						}
					}
				}

				// 发起请求
				var result = await this.$get(
					'https://ccdcapi.alipay.com/validateAndCacheCardInfo.json?_input_charset=utf-8&cardNo=' + val +
					'&cardBinCheck=true'
				).then(function(res) {
					return res;
				})
				// console.log(result);
				//6217996100080404583
				// 6228480831781064918
				// 622908553020636611
				// 判断当前添加的银行卡是否正确
				if (result.validated == true) {

					// DC: "储蓄卡",
					//    CC: "信用卡",
					//    SCC: "准贷记卡",
					//    PC: "预付费卡"
					// 判断卡号类型
					if (result.cardType == 'DC') {
						result.cardType = '储蓄卡';
					}
					if (result.cardType == 'CC') {
						result.cardType = '信用卡';
					}
					if (result.cardType == 'SCC') {
						result.cardType = '准贷记卡';
					}
					if (result.cardType == 'PC') {
						result.cardType = '预付费卡';
					}

					var carkType = bankInfo.bankList[result.bank] + result.cardType;
					// 放银行卡号之前先清除缓存
					localStorage.removeItem('cardNum');
					localStorage.setItem('cardNum', val);
					this.$router.push({
						path: '/phone_number_check',
						query: {
							carkType: carkType,
						}
					})


				} else {
					Toast.fail('添加失败');
				}
			},
			checkCard() {
				var but = document.querySelector(".van-button__content");
				var b = document.getElementsByTagName("button")[0];
				if (but != null && b != undefined) {
					if (this.digitVal != "") {
						but.style.background = "#108ee9";
						but.style.color = "#FFFFFF";
						this.val = false;
					} else {
						but.style.background = "#FFFFFF";
						but.style.color = "#999999";
						this.val = true;
					}
				}
			}
		}

	}
</script>

<style>
	@import '../../assets/css/add_bank_card.css';
</style>
