<template>
	<div id="ph_ch_co">
		<div id="ph_ch_co_head">
			<van-nav-bar :border="false" left-text="验证手机号" left-arrow @click-left="onClickLeft" />
		</div>
		<div id="ph_ch_co_content">
			<div class="md-example-child md-example-child-captcha-1" @keydown="headCode()">
			    <md-captcha
			      :maxlength="4"
			      :isView="true"
				  @submit="headCode"
			      brief="最新验证码依然有效，请勿重发"
			    >
			      验证码已发送至{{phoneNum}}
			    </md-captcha>
			  </div>
		</div>	
	</div>
</template>

<script>
	import bankInfo from '../../utils/banks.js';
	import {
		NavBar,
		Toast
	} from 'vant';
	import {
		Captcha
	} from 'mand-mobile';
	export default {
		name: 'captcha-demo',
		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Captcha.name]: Captcha,
		},
		data() {
			return {
				digitVal: '',
				show: false,
				phoneNum: this.$route.query.phoneNum,
				right_icon: "info",
				val: true,
				bankList: [],
				cardNo: '',
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			headCode(val){
				// console.log(val);
				this.nextStep();
			},
			async nextStep() {
				// 拿出银行卡号
				var val=localStorage.getItem('cardNum');
				// console.log(val);
				// 发起请求
				var result = await this.$get(
					'https://ccdcapi.alipay.com/validateAndCacheCardInfo.json?_input_charset=utf-8&cardNo=' + val +
					'&cardBinCheck=true'
				).then(function(res) {
					return res;
				})

				//邮政银行 6217996100080404583
				//农业银行 6228480831781064918
				//兴业银行 622908553020636611
				//建设银行 6236683480001168113 
				// 判断当前添加的银行卡是否正确
				if (result.validated == true) {

					// DC: "储蓄卡",
					// CC: "信用卡",
					// SCC: "准贷记卡",
					// PC: "预付费卡"
					// 判断卡号类型
					if (result.cardType == 'DC') {
						result.cardType = '储蓄卡';
					}
					if (result.cardType == 'CC') {
						result.cardType = '信用卡';
					}
					if (result.cardType == 'SCC') {
						result.cardType = '准贷记卡';
					}
					if (result.cardType == 'PC') {
						result.cardType = '预付费卡';
					}


					//  获取当前用户的银行卡  发卡行中文名称

					var bankName = bankInfo.bankList[result.bank];

					//创建一个储存所有银行卡信息
					let arrAll = [];
					
					// 将每一张银行卡信息存到一个对象中
					let obj = {
						cardType: result.cardType,
						cardNo: result.key,
						bankName: bankName,
						Icon: result.bank
					};
					// 判断加背景色
					if (result.key == "中国工商银行") {
						obj.color = "#c13d4b";
					} else if (bankName == "中国农业银行") {
						obj.color = "#3b8783";
					} else if (bankName == "中国邮政储蓄银行") {
						obj.color = "#37827d";
					} else if (bankName == "中国建设银行") {
						obj.color = "#486ea7";
					} else if (bankName == "兴业银行") {
						obj.color = "#224588";
					} else {
						obj.color = "#3d8a86";
					}
					
					// console.log(obj);
					
					if (localStorage.getItem("bankList")!=null) {
						// console.log(111);
						arrAll = JSON.parse(localStorage.getItem("bankList"));
						// console.log(arrAll);
					} else {
						// console.log(222)
						arrAll = [];
					}
					// 将每张银行卡对象添加到数组中
					arrAll.push(obj);
					// console.log(arrAll);

					// 把新添加的银行卡信息放进本地缓存中
					arrAll = JSON.stringify(arrAll); //转化为JSON字符串
					localStorage.setItem("bankList", arrAll);
					
					Toast.setDefaultOptions({
						duration: 1000
					});	
					Toast.success('添加成功');
					setTimeout(() => {
						this.$router.push('/bank_card');
					}, 1000);


				} else {
					Toast.fail('添加失败');
				}
			}
		}
	}
</script>
<style lang="stylus">
.md-example-child-captcha-1
  height 650px
  padding 30px 0
  .md-number-keyboard
    margin-top 30px
</style>
<style>
	#ph_ch_co {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0px;
		background-color: #f5f5f9;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar {
		background-color: #FFFFFF;
		height: 1rem;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar__text {
		font-size: 0.35rem;
		color: #333333;
	}

	#ph_ch_co #ph_ch_co_head .van-nav-bar .van-icon-arrow-left::before {
		font-size: 0.5rem;
		color: #1989fa;
	}
	
	#ph_ch_co_content{

	}
	#ph_ch_co #ph_ch_co_content .md-captcha-footer{
		padding: 0 0.2rem;
		font-size: 0.3rem;
		height: 1rem;
	}
	#ph_ch_co #ph_ch_co_content .md-captcha-btn{
		font-size: 0.35rem;
	}
	
	#ph_ch_co #ph_ch_co_content .md-captcha-content{
		font-size: 0.35rem;
	}
</style>
