<template>
	<div id="set_up">
		<div id="setUp_head">
			<van-nav-bar title="设置" left-text="资产" left-arrow @click-left="onClickLeft" :border="false" />
		</div>
		<div class="oneTwoPb">
			<van-cell title="账户安全" is-link to="/account_security" />
			<van-cell title="支付设置" is-link to="/pay_settings" />
			<van-cell title="密码设置" is-link to="/paw_set" />
		</div>
		<div class="oneTwoPb">
			<van-cell title="新消息通知" is-link to="/news_set" />
			<van-cell title="隐私" is-link to="/privacy_set" />
			<van-cell title="通用" is-link to="/currency" />
		</div>
		<div class="three">
			<van-cell title="关于" is-link to="/about" />
		</div>
		<div class="button">
			<van-button type="primary" size="large" @click="showPopup(1)">退出登录</van-button>
		</div>
		<van-popup v-model="show" :close-on-click-overlay="false">
			<div class="sign_out">退出登录</div>
			<div class="choice">
				<span @click="showPopup(2)">确定</span>
				<span @click="showPopup(0)">取消</span>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import {
		NavBar,
		Toast,
		Cell,
		CellGroup,
		Button,
		Popup
	} from 'vant';
	export default {
		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Cell.name]: Cell,
			[CellGroup.name]: CellGroup,
			[Button.name]: Button,
			[Popup.name]: Popup
		},
		methods: {
			onClickLeft() {
				this.$router.push('/assets');
			},

			showPopup(value) {
				switch (value) {
					case 0:
						this.show = false;
						break;
					case 1:
						this.show = true;
						break;
					case 2:
						this.$router.push('/login');
						break;
				}
			}
		},
		data() {
			return {
				active: 0,
				show: false
			};
		}
	};
</script>

<style>
	@import '../../assets/css/set_up.css';
</style>
