<template>
	<div id="bank_card">
		<div id="bank_card_head">
			<van-nav-bar title="银行卡" :border="false" :fixed="true" left-text="返回" left-arrow @click-left="onClickLeft"
			 @click-right="onClickRight">
				<template #right>
					<van-icon name="plus" size="18" />
				</template>
			</van-nav-bar>
		</div>
		<div id="bank_card_content">
			<div class="bank_card_info" :style="{'background-color':val.color}" v-for="(val, k) in bankList" :key="k">
				<div class="bank_card_logo">
					<van-image style="margin-left: 0.1rem;" width="2rem" height="2rem" fit="contain" :src="require('@/assets/small_logo/'+val.Icon+'.png')" />
				</div>
				<div class="bank_card_right">
					<div class="bank_card_name">{{val.bankName}}<i style="font-size: 0.2rem;margin-left: 0.2rem; color: #bbb;">快捷支付</i></div>
					<div class="bank_card_type">{{val.cardType}}</div>
					<div class="bank_card_id">
						<span>****</span><span>****</span><span>****</span><span>{{val.cardNo.substr(val.cardNo.length - 4, 4)}}</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		NavBar,
		Toast,
		Icon,
		Image as VanImage
	} from 'vant';
	export default {

		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Icon.name]: Icon,
			[VanImage.name]: VanImage
		},
		data() {
			return {
				bankList: [],
			}
		},
		// 生命周期函数
		async created() {
			// 拿出所有银行卡信息
			let arrAll = JSON.parse(localStorage.getItem("bankList"));
			// console.log(arrAll);
			this.bankList = arrAll;
			// localStorage.clear();
		},
		methods: {
			onClickLeft() {
				this.$router.push('/assets');
				// this.$router.go('-1');
			},
			onClickRight() {
				this.$router.push('/add_bank_card');
			},
		}
	}
</script>

<style>
	#bank_card {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0px;
		background-color: #272c33;
		overflow-y: scroll;
		touch-action: pan-y;
	}

	#bank_card #bank_card_head .van-nav-bar {
		background-color: #272c33;
		height: 1rem;
	}

	#bank_card #bank_card_head .van-nav-bar__title {
		font-size: 0.35rem;
		color: #FFFFFF;
	}


	#bank_card #bank_card_head .van-nav-bar__text {
		font-size: 0.32rem;
		color: #FFFFFF;
	}

	#bank_card #bank_card_head .van-icon-arrow-left::before {
		font-size: 0.4rem;
		color: #FFFFFF;
	}

	#bank_card #bank_card_head .van-icon-plus::before {
		font-size: 0.5rem;
		color: #FFFFFF;
	}

	/* 银行卡信息 */
	#bank_card_content {
		padding-top: 1.4rem;
		width: 98%;
		margin: 0 auto;
	}

	#bank_card_content .bank_card_info {
		width: 100%;
		height: 2.5rem;
		background-color: #bf3c4a;
		border-radius: 0.1rem;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin-top: -0.2rem;
		box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.4);
	}

	/* 银行卡logo */
	#bank_card_content .bank_card_info .bank_card_logo {
		width: 0.8rem;
		height: 0.8rem;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #FFFFFF;
		border-radius: 50%;
		margin-right: 0.2rem;
		margin-top: -0.6rem;
	}

	/* 银行卡右边模块 */
	#bank_card_content .bank_card_info .bank_card_right {
		width: 5.8rem;
		height: 1.6rem;
		/* border: 1px solid white; */
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}

	/* 银行卡名称 */
	#bank_card_content .bank_card_info .bank_card_right .bank_card_name {
		font-size: 0.35rem;
		color: #FFFFFF;
	}

	/* 银行卡类型 */
	#bank_card_content .bank_card_info .bank_card_right .bank_card_type {
		font-size: 0.25rem;
		color: #FFFFFF;
	}

	/* 银行卡号 */
	#bank_card_content .bank_card_info .bank_card_right .bank_card_id {
		font-size: 0.35rem;
		color: #FFFFFF;
	}

	#bank_card_content .bank_card_info .bank_card_right .bank_card_id span {
		margin-right: 0.75rem;
		letter-spacing: 0.05rem;
		color: #e7a0a8;
	}

	#bank_card_content .bank_card_info .bank_card_right .bank_card_id :last-child {
		font-size: 0.5rem;
		letter-spacing: 0;
		color: #FFFFFF;
	}
</style>
