<template>
	<div id="ph_nb_che">
		<div id="ph_nb_che_head">
			<van-nav-bar :border="false" left-text="填写银行卡信息" left-arrow @click-left="onClickLeft" />
		</div>
		<div style="width: 100%; height: 0.2rem;background-color: #f5f5f9;"></div>
		<div id="ph_nb_che_from">
			<van-cell-group>
				<van-field label="卡类型" :value="type" readonly />
				<div style="width: 100%; height: 0.2rem;background-color: #f5f5f9;"></div>
				<van-field v-model="digitVal" type="digit" v-bind:right-icon="right_icon" @click-right-icon="onClickRight(1)"
				 autofocus="true" label="手机号" maxlength="11" placeholder="银行预留手机号码" :clearable="true" :clear="checkCard()" />
			</van-cell-group>
		</div>
		<div id="Tips1">同意<span style="color: #108ee9;">《服务协议》</span></div>
		<div id="submit">
			<van-button @click="nextStep()" type="primary" size="large" v-bind:disabled="val">下一步</van-button>
		</div>
		<div>
			<van-popup v-model="show" :close-on-click-overlay="false">
				<span>手机号说明</span>
				<div style="line-height: 0.6rem;">银行卡预留的手机号是办理该银行卡时的手机号码。没有预留、手机号忘记或者已停用，请联系银行客服更新处理。大陆手机号为11位数字，非大陆手机号为“国家代码-手机号码”形式。</div>
				<p @click="onClickRight(0)">知道了</p>
			</van-popup>
		</div>
	</div>
</template>

<script>
	import bankInfo from '../../utils/banks.js'
	import {
		NavBar,
		Toast,
		Icon,
		Field,
		Button,
		Popup
	} from 'vant';
	export default {

		components: {
			[NavBar.name]: NavBar,
			[Toast.name]: Toast,
			[Icon.name]: Icon,
			[Field.name]: Field,
			[Button.name]: Button,
			[Popup.name]: Popup
		},
		data() {
			return {
				digitVal: '',
				show: false,
				type: this.$route.query.carkType,
				right_icon: "info",
				val: true,
			}
		},
		methods: {
			onClickLeft() {
				this.$router.go(-1);
			},
			onClickRight(num) {
				if (num == 1) {
					this.show = true;
				} else {
					this.show = false;
				}
			},
			async nextStep() {
				var val = this.digitVal;
				if(!(/^1[3456789]\d{9}$/.test(this.digitVal))){
				    Toast.fail('请填写正确的手机号码');  
				    return false; 
				} 
				var phoneNum=val.substr(0,3)+"*****"+val.substr(9);	
					this.$router.push({
						path: '/phone_check_code',
						query: {
							phoneNum: phoneNum
						}
					})
			},
			checkCard() {
				var but = document.querySelector(".van-button__content");
				var b = document.getElementsByTagName("button")[0];
				if (but != null && b != undefined) {
					if (this.digitVal != "") {
						// 隐藏右侧提示图标
						this.right_icon = "";
						but.style.background = "#108ee9";
						but.style.color = "#FFFFFF";
						b.disabled = false;
						this.val = false;
					} else {
						// 显示右侧提示图标
						this.right_icon = "info";
						but.style.background = "#FFFFFF";
						but.style.color = "#999999";
						this.val = true;
					}
				}
			}
		}	
	}
</script>

<style>
	#ph_nb_che {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0px;
		background-color: #f5f5f9;
	}

	#ph_nb_che #ph_nb_che_head .van-nav-bar {
		background-color: #FFFFFF;
		height: 1rem;
	}

	#ph_nb_che #ph_nb_che_head .van-nav-bar__text {
		font-size: 0.35rem;
		color: #333333;
	}

	#ph_nb_che #ph_nb_che_head .van-nav-bar .van-icon-arrow-left::before {
		font-size: 0.5rem;
		color: #1989fa;
	}

	#ph_nb_che #Tips1 {
		font-size: 0.3rem;
		/* border: 1px solid firebrick; */
		height: 0.8rem;
		line-height: 0.8rem;
		padding-left: 0.3rem;
		color: #999999;
	}

	#ph_nb_che #ph_nb_che_from .van-cell {
		height: 1rem;
		line-height: 0.8rem;
		font-size: 0.35rem;
	}

	#ph_nb_che #ph_nb_che_from .van-field__label {
		width: 1.4rem;
	}

	#ph_nb_che #ph_nb_che_from .van-icon::before {
		font-size: 0.5rem;
		color: #108ee9;
		position: absolute;
		top: -0.42rem;
		right: 0rem;
	}

	#ph_nb_che #submit {
		display: flex;
		justify-content: center;
		margin-top: 0.2rem;
	}

	#ph_nb_che #submit .van-button--large {
		width: 90%;
		height: 0.8rem;
		background: #ffffff;
		color: #999999;
		font-size: 0.35rem;
		border: 2px solid #e5e5e5;
	}

	/* 提示 */
	body .van-toast {
		height: 1.5rem;
		width: 3.4rem;
		font-size: 0.4rem;
		line-height: 0.7rem;
	}

	/* 弹出层 */
	#ph_nb_che .van-popup--center {
		width: 5rem;
		height: 5.4rem;
		font-size: 0.35rem;
		padding: 0 0.3rem 0.3rem;
	}

	#ph_nb_che .van-popup--center span {
		line-height: 1rem;
		font-size: 0.45rem;
		color: #333333;
	}

	#ph_nb_che .van-popup--center p {
		color: #1989fa;
		position: absolute;
		right: 0.3rem;
		bottom: 0.3rem;
	}
</style>
