<template>
	<div id="pbBg">
		<app-head titleVla="新消息通知" left_text="设置"></app-head>
		<div id="ac_ce_content">
			<van-cell-group>
				<van-switch-cell v-model="checked" title="接受推送通知" @click="all()" />
				<div id="ac_ce_content_option" style="display: none;">
					<van-cell title="不接受消息时间" is-link value="未设置" @click="showPopup" />
					<div class="ac_ce_content_tips">
						社区消息通知设置
					</div>
					<van-switch-cell v-model="checked1" title="邀请我回答" />
					<van-switch-cell v-model="checked2" title="回答我" />
					<van-switch-cell v-model="checked3" title="评论和赞" />
					<van-switch-cell v-model="checked4" title="打赏我" />
					<van-switch-cell v-model="checked5" title="头条" />
					<van-switch-cell v-model="checked6" title="股价提醒" />
					<van-switch-cell v-model="checked7" title="个股公告" />
					<van-switch-cell v-model="checked8" title="系统消息" />
				</div>
			</van-cell-group>
			<van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
				<van-datetime-picker v-model="currentTime" type="time" title="选择时间" :min-hour="10" :max-hour="20" />
			</van-popup>
		</div>
	</div>
</template>

<script>
	import AppHead from '@/components/head.vue'
	import {
		SwitchCell,
		DatetimePicker,
		Popup,
		RadioGroup,
		Radio
	} from 'vant';
	export default {
		components: {
			AppHead,
			[SwitchCell.name]: SwitchCell,
			[DatetimePicker.name]: DatetimePicker,
			[Popup.name]: Popup,
			[RadioGroup.name]: RadioGroup,
			[Radio.name]: Radio
		},
		data() {
			return {
				checked: false,
				checked1: true,
				checked2: true,
				checked3: true,
				checked4: true,
				checked5: true,
				checked6: true,
				checked7: true,
				checked8: true,
				currentTime: '0:00',
				show: false,
			}
		},
		methods: {
			onInput(checked) {
				Dialog.confirm({
					title: '提醒',
					message: '是否切换开关？',
				}).then(() => {
					this.checked = checked;
				});
			},
			showPopup() {
				this.show = true;
			},
			all() {
				let option = document.getElementById("ac_ce_content_option");
				if(option.style.display=="block"){
					option.style.display="none";
				}else{
					option.style.display="block";
				}
			}
		},
	}
</script>

<style>
	#pbBg #ac_ce_content .ac_ce_content_tips {
		width: 96%;
		padding-left: 4%;
		height: 0.8rem;
		background-color: #f5f5f9;
		font-size: 0.3rem;
		line-height: 0.8rem;
		color: #999999;
	}

	/* 时间 */
	#pbBg #ac_ce_content .van-picker__toolbar {
		height: 0.8rem;

	}

	#pbBg #ac_ce_content .van-picker__cancel {
		font-size: 0.3rem;
	}

	#pbBg #ac_ce_content .van-picker__confirm {
		font-size: 0.3rem;
	}

	#pbBg #ac_ce_content .van-picker__title {
		font-size: 0.35rem;
		height: 0.5rem;
		line-height: 0.5rem;
	}

	#pbBg #ac_ce_content .van-ellipsis {
		font-size: 0.3rem;
	}
	#ac_ce_content_option{
		display: block;
	}
</style>
