import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router' //引入vue-Router
import router from "./router/router.js"

// 引入axios
import Axios from 'axios'
// 引入qs
// import qs from 'qs'
// 引用刚才封装的请求方法
import {
	post,
	get
} from './utils/http.js'

Vue.prototype.$axios = Axios
Vue.prototype.$post = post
Vue.prototype.$get = get
//mandmobile
import "mand-mobile/components/_style/global.styl";
import "normalize.css";

//index图标
import './assets/indexicon/iconfont.css';
// home图标
import './assets/home_icon/iconfont.css';
// assets图标
import "./assets/assets_icon/iconfont.css"
// vant
import 'vant/lib/index.css';
import {
	Tabbar,
	TabbarItem,
	Icon
} from 'vant';
Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Icon);

Vue.use(VueRouter); //全局使用  vue-ROuter

Vue.config.productionTip = false

new Vue({
	render: h => h(App),
	router, //注入到根实例中
}).$mount('#app')
