<template>
	<div id="pbBg">
		<app-head titleVla="隐私设置" left_text="设置"></app-head>
		<div id="pr_set_content">
			<div class="pr_set_content_tips">
				<div>自选股</div>
				<div>设置谁可以看我的自选股</div>
			</div>
			<van-cell-group>
				<van-cell title="所有人" @click="cellRadio(0)">
					<template #right-icon>
						<van-icon name="success" style="display: none;" />
					</template>
				</van-cell>
				<van-cell title="关注的人" @click="cellRadio(1)">
					<template #right-icon>
						<van-icon name="success" style="display: none;" />
					</template>
				</van-cell>
				<van-cell title="不显示" @click="cellRadio(2)">
					<template #right-icon>
						<van-icon name="success" style="display: block;" />
					</template>
				</van-cell>
			</van-cell-group>

			<div class="pr_set_content_fot">
				通过支付宝关系搜索到我
			</div>
			<van-cell-group>
				<van-cell title="打开" @click="cellRadio2(3)">
					<template #right-icon>
						<van-icon :name="name1" />
					</template>
				</van-cell>
				<van-cell title="关闭" @click="cellRadio2(4)">
					<template #right-icon>
						<van-icon :name="name2" />
					</template>
				</van-cell>
			</van-cell-group>
		</div>
	</div>
</template>

<script>
	import AppHead from '@/components/head.vue'
	import {
		SwitchCell,
		Toast
	} from 'vant';
	export default {
		components: {
			AppHead,
			[SwitchCell.name]: SwitchCell,
			[Toast.name]: Toast
		},
		data() {
			return {
				checked: true,
				name1: '',
				name2: 'success'
			}
		},
		methods: {
			cellRadio(num) {
				var d = document.getElementsByClassName('van-icon-success');
				for (var i = 0; i < d.length - 2; i++) {
					if (num == i) {
						d[2].style.display="none";
						d[i].style.display = "block";
						Toast.success('设置成功');
						continue;
					} else {
						d[i].style.display = "none";
					}
				}
			},
			cellRadio2(num2){
				switch(num2){
					case 3:
					this.name1="success";
					this.name2="";
					Toast.success('设置成功');
					break;
					case 4:
					this.name2="success";
					this.name1="";
					Toast.success('设置成功');
					break;
				}
			}
		},
	}
</script>

<style>
	#pbBg #pr_set_content .pr_set_content_tips {
		width: 96%;
		height: 1rem;
		/* border: 1px solid lawngreen; */
		padding: 0.2rem 0 0 4%;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		background-color: #f1f6f8;
	}

	#pbBg #pr_set_content .pr_set_content_tips :first-child {
		font-size: 0.3rem;
		color: #5d5d5d;
	}

	#pbBg #pr_set_content .pr_set_content_tips :last-child {
		font-size: 0.26rem;
		color: #9a9b9b;
	}

	#pbBg #pr_set_content .pr_set_content_fot {
		width: 96%;
		padding-left: 4%;
		height: 0.6rem;
		background-color: #f1f6f8;
		font-size: 0.3rem;
		line-height: 0.6rem;
		color: #5d5d5d;
	}

	/* 打勾图标 */
	#pr_set_content .van-icon-success::before {
		font-size: 0.5rem;
		color: #1989fa;
	}
</style>
